<template>
  <div class="app-container">
    <MainView 
      v-if="!isCardView" 
      :rulesCardSrc="rulesCardSrc"
      @start-game="startCardView"
      ref="mainView"
    />
    
    <CardView 
      v-else 
      :cardBackSrc="cardBackSrc"
      :excludedCards="excludedCards"
      @return-to-main="returnToMain"
    />
  </div>
</template>

<script>
import MainView from './MainView.vue';
import CardView from './CardView.vue';

export default {
  name: 'AppHome',
  components: {
    MainView,
    CardView
  },
  props: {
    code: String // 路由参数
  },
  data() {
    return {
      isCardView: false,
      rulesCardSrc: 'https://s2.loli.net/2025/04/26/R5nBQ9hqZKGf6jI.jpg',
      cardBackSrc: 'https://s2.loli.net/2025/04/26/a2wjXmz8En6HNRC.jpg',
      excludedCards: []
    }
  },
  created() {
    // 如果通过扫码进入，自动填入识别码
    this.checkRouteCode();
  },
  watch: {
    // 监听路由变化
    $route() {
      this.checkRouteCode();
    }
  },
  methods: {
    // 检查路由中的识别码
    checkRouteCode() {
      if (this.$route.params.code) {
        // 如果路由包含识别码，重置状态
        this.isCardView = false;
        
        // 在下一个周期通知MainView组件使用此识别码
        this.$nextTick(() => {
          // 找到MainView组件实例
          const mainViewRef = this.$refs.mainView;
          if (mainViewRef && this.$route.params.code) {
            // 只设置识别码，不自动开始游戏
            mainViewRef.identityCode = this.$route.params.code;
          }
        });
      }
    },
    
    // 开始抽卡视图
    startCardView(excludedCards = []) {
      this.excludedCards = excludedCards;
      this.isCardView = true;
    },
    
    // 返回主界面
    returnToMain() {
      this.isCardView = false;
      this.excludedCards = [];
    }
  }
}
</script>

<style scoped>
.app-container {
  width: 100%;
  margin: 0 auto;
  padding: 20px;
  box-sizing: border-box;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
</style> 