import { createRouter, createWebHashHistory } from 'vue-router'
import LoginView from './components/LoginView.vue'
import AppHome from './components/AppHome.vue'

const routes = [
  {
    path: '/',
    component: LoginView
  },
  {
    path: '/home',
    component: AppHome,
    meta: { requiresAuth: true }
  },
  {
    path: '/scan/:code',
    component: AppHome,
    props: true
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

// 添加路由守卫
router.beforeEach((to, from, next) => {
  // 如果访问需要验证的页面，检查是否已认证
  if (to.matched.some(record => record.meta.requiresAuth)) {
    // 如果没有通过扫码链接进入且没有认证，则跳转到登录页面
    if (!localStorage.getItem('isAuthenticated') && !to.params.code) {
      next({ path: '/' })
    } else {
      next()
    }
  } else {
    // 如果已认证且访问登录页，则跳转到主页
    if (to.path === '/' && localStorage.getItem('isAuthenticated')) {
      next({ path: '/home' })
    } else {
      next()
    }
  }
})

export default router 