<template>
  <div class="card-view" :class="{ 'mobile-view': isMobile }">
    <h2>抽卡结果</h2>
    <div class="cards-container">
      <div 
        v-for="(card, index) in cards" 
        :key="index" 
        class="card"
        :class="{ 
          'flipped': showCardFront ? card.isFlipped : !card.isFlipped,
          'special-card': isSpecialCard(card.id) && (showCardFront ? !card.isFlipped : card.isFlipped)
        }"
        @click="flipCard(index)"
      >
        <div class="card-inner">
          <div class="card-front">
            <img :src="getCardFrontSrc(card.id)" alt="卡牌正面">
          </div>
          <div class="card-back">
            <img :src="cardBackSrc" alt="卡牌背面">
          </div>
        </div>
      </div>
    </div>
    
    <div class="buttons">
      <button class="action-button" @click="handleFlipAllCards" :disabled="allCardsFlipped">抽取卡牌</button>
      <button class="return-button" @click="returnToMain">返回主界面</button>
      <button class="toggle-button" @click="toggleCardsFace">{{ showCardFront ? '隐藏' : '显示' }}</button>
    </div>
    
    <div class="code-container" v-if="identityCode">
      <p>识别码: <span class="identity-code">{{ identityCode }}</span></p>
      <p class="code-tip">将此识别码发给对方，对方输入后可避免抽到相同卡牌</p>
      
      <div class="qrcode-container" v-if="qrcodeUrl">
        <p class="qrcode-title">或扫描下方二维码</p>
        <img :src="qrcodeUrl" alt="二维码" class="qrcode-image">
      </div>
    </div>
  </div>
</template>

<script>
import QRCode from 'qrcode';

export default {
  name: 'CardView',
  props: {
    cardBackSrc: {
      type: String,
      required: true
    },
    excludedCards: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      cards: [],
      allCardsFlipped: false,
      showCardFront: false,
      inDrawMode: false,
      identityCode: '',
      qrcodeUrl: '',
      specialCardIds: [21, 22, 46, 47, 48, 50], // 功能牌ID
      isMobile: false
    }
  },
  created() {
    this.drawCards();
    this.checkDeviceType();
    // 监听窗口大小变化
    window.addEventListener('resize', this.checkDeviceType);
  },
  beforeUnmount() {
    // 移除事件监听器
    window.removeEventListener('resize', this.checkDeviceType);
  },
  methods: {
    // 检查设备类型
    checkDeviceType() {
      this.isMobile = window.innerWidth <= 768;
    },
    
    // 检查是否为特殊功能牌
    isSpecialCard(id) {
      return this.specialCardIds.includes(id);
    },
    
    // 抽取5张随机卡牌
    drawCards() {
      this.cards = [];
      this.allCardsFlipped = false;
      this.showCardFront = false;
      this.inDrawMode = false;
      
      // 生成1-54的随机数组（排除已抽取的卡牌）
      let availableCards = Array.from({ length: 54 }, (_, i) => i + 1)
        .filter(id => !this.excludedCards.includes(id));
      
      // 随机打乱
      for (let i = availableCards.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [availableCards[i], availableCards[j]] = [availableCards[j], availableCards[i]];
      }
      
      // 选择5张牌
      const selectedCards = availableCards.slice(0, 5);
      
      // 添加到卡牌列表
      for (let i = 0; i < selectedCards.length; i++) {
        this.cards.push({
          id: selectedCards[i],
          isFlipped: false
        });
      }
      
      // 生成识别码
      this.generateIdentityCode();
    },
    
    // 生成识别码
    generateIdentityCode() {
      const cardIds = this.cards.map(card => card.id);
      
      // 将卡牌ID转换为更短的编码
      let code = '';
      
      for (const id of cardIds) {
        // 0-9, a-z, A-Z 的62进制表示，每个卡牌只占用1位
        const charSet = '0123456789abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ';
        // 卡牌ID从1-54，转为索引0-53
        const index = id - 1;
        // 添加对应字符
        code += charSet[index];
      }
      
      this.identityCode = code;
      
      // 生成二维码
      this.generateQRCode(code);
    },
    
    // 生成二维码
    generateQRCode(code) {
      // 创建一个临时canvas元素
      const canvas = document.createElement('canvas');
      
      // 使用QRCode.js生成二维码
      QRCode.toCanvas(canvas, window.location.origin + '/#/scan/' + code, {
        width: 200,
        margin: 2,
        color: {
          dark: '#4c6ef5',
          light: '#ffffff'
        }
      }, (error) => {
        if (error) {
          console.error('生成二维码出错:', error);
          return;
        }
        
        // 将canvas转换为图片URL
        this.qrcodeUrl = canvas.toDataURL('image/png');
      });
    },
    
    // 翻转所有卡牌
    handleFlipAllCards() {
      // 进入抽取模式，卡牌设置为背面朝上
      this.inDrawMode = true;
      
      // 将显示状态设置为false（背面）
      this.showCardFront = false;
      
      // 设置所有卡牌为已翻开状态
      this.cards.forEach(card => {
        card.isFlipped = false;
      });
      this.allCardsFlipped = true;
    },
    
    // 翻转单张卡牌
    flipCard(index) {
      if (this.allCardsFlipped) {
        this.cards[index].isFlipped = !this.cards[index].isFlipped;
      }
    },
    
    // 切换卡牌正反面
    toggleCardsFace() {
      this.showCardFront = !this.showCardFront;
      
      // 重置所有卡牌状态，确保所有卡牌统一显示
      this.cards.forEach(card => {
        card.isFlipped = false; // 统一重置为初始状态
      });
    },
    
    // 获取卡牌正面图片路径
    getCardFrontSrc(id) {
      // 格式化卡牌ID为两位数
      const formattedId = id.toString().padStart(2, '0');
      
      // 根据图床链接文件中的URL映射
      const cardUrls = {
        '01': 'https://s2.loli.net/2025/04/26/IPAMOtKgoElXNhL.jpg',
        '02': 'https://s2.loli.net/2025/04/26/815HcyZwu3jhMFI.jpg',
        '03': 'https://s2.loli.net/2025/04/26/kNJ6wUvAj3VuoX8.jpg',
        '04': 'https://s2.loli.net/2025/04/26/qk6Z3oUadCvMTxW.jpg',
        '05': 'https://s2.loli.net/2025/04/26/nUEmLMbCgx5j1OX.jpg',
        '06': 'https://s2.loli.net/2025/04/26/CRBmkYMhoK5cbwx.jpg',
        '07': 'https://s2.loli.net/2025/04/26/ojc1U8Zy6lLDbnX.jpg',
        '08': 'https://s2.loli.net/2025/04/26/VDY54HBjCMW2dgn.jpg',
        '09': 'https://s2.loli.net/2025/04/26/pGIKRg91zAwCVjY.jpg',
        '10': 'https://s2.loli.net/2025/04/26/c4TWf1gDFEiHLUG.jpg',
        '11': 'https://s2.loli.net/2025/04/26/jDVTvJs4hEORPpS.jpg',
        '12': 'https://s2.loli.net/2025/04/26/yDwlTkzZEoAn7gX.jpg',
        '13': 'https://s2.loli.net/2025/04/26/g8MP1DfJzSvrTp7.jpg',
        '14': 'https://s2.loli.net/2025/04/26/ON53jUgciVeLaKG.jpg',
        '15': 'https://s2.loli.net/2025/04/26/HV9WsG56KA1STjQ.jpg',
        '16': 'https://s2.loli.net/2025/04/26/3qm8ptP9KZvY67r.jpg',
        '17': 'https://s2.loli.net/2025/04/26/ndjkY39RApytuTZ.jpg',
        '18': 'https://s2.loli.net/2025/04/26/v63MmcnOsuEPC2w.jpg',
        '19': 'https://s2.loli.net/2025/04/26/1bF7R94Kpdo8SGU.jpg',
        '20': 'https://s2.loli.net/2025/04/26/c3kxrvhH4y6OguU.jpg',
        '21': 'https://s2.loli.net/2025/04/26/IuatYBZ4dPJpniF.jpg',
        '22': 'https://s2.loli.net/2025/04/26/isBuYwfyWCtJk7E.jpg',
        '23': 'https://s2.loli.net/2025/04/26/ZBYKrPqneu8xCdI.jpg',
        '24': 'https://s2.loli.net/2025/04/26/uCwklA7hOofRFJW.jpg',
        '25': 'https://s2.loli.net/2025/04/26/9Ikw7LQirzlgB5n.jpg',
        '26': 'https://s2.loli.net/2025/04/26/PGik7J3IO1xTKht.jpg',
        '27': 'https://s2.loli.net/2025/04/26/zPWDrbeKNfi2LF9.jpg',
        '28': 'https://s2.loli.net/2025/04/26/iUGblh8rPDz9RIY.jpg',
        '29': 'https://s2.loli.net/2025/04/26/M35HaYywEXuTc1J.jpg',
        '30': 'https://s2.loli.net/2025/04/26/pL7eGHr3n1WZyEY.jpg',
        '31': 'https://s2.loli.net/2025/04/26/ungZivazUY7x5r9.jpg',
        '32': 'https://s2.loli.net/2025/04/26/zDfxKIoE2WOqwNC.jpg',
        '33': 'https://s2.loli.net/2025/04/26/AS2FZmxoGR4EiWB.jpg',
        '34': 'https://s2.loli.net/2025/04/26/nBu3mgWfpycbo7w.jpg',
        '35': 'https://s2.loli.net/2025/04/26/xUYnpgrsowFOq4f.jpg',
        '36': 'https://s2.loli.net/2025/04/26/wn3ez5uOkRYdrHV.jpg',
        '37': 'https://s2.loli.net/2025/04/26/EFW58oPrybJzl1X.jpg',
        '38': 'https://s2.loli.net/2025/04/26/L7aoYjOeT4npASi.jpg',
        '39': 'https://s2.loli.net/2025/04/26/dtEY1WRlxirQJhf.jpg',
        '40': 'https://s2.loli.net/2025/04/26/dR2OkzNrobSKH4W.jpg',
        '41': 'https://s2.loli.net/2025/04/26/TMdkoDPEylqKC6J.jpg',
        '42': 'https://s2.loli.net/2025/04/26/yHsb7TDiuUBhJ1a.jpg',
        '43': 'https://s2.loli.net/2025/04/26/MNjidFkobhWt6Ac.jpg',
        '44': 'https://s2.loli.net/2025/04/26/SRnm6QgbZ8d12VY.jpg',
        '45': 'https://s2.loli.net/2025/04/26/x3fkXKDjZVNSF6C.jpg',
        '46': 'https://s2.loli.net/2025/04/26/sB3IPDazvK1bgXS.jpg',
        '47': 'https://s2.loli.net/2025/04/26/RmCzWT7yqQa91jB.jpg',
        '48': 'https://s2.loli.net/2025/04/26/WaEBfgCztpK6IbV.jpg',
        '49': 'https://s2.loli.net/2025/04/26/3nsiWFkRuo1cq6J.jpg',
        '50': 'https://s2.loli.net/2025/04/26/b9FKUsNOygpH2Rz.jpg',
        '51': 'https://s2.loli.net/2025/04/26/ZEFWAIUqvbtBK3T.png',
        '52': 'https://s2.loli.net/2025/04/26/ClkBshSMw28XRTo.png',
        '53': 'https://s2.loli.net/2025/04/26/URBKdtCzYcDXJ3x.png',
        '54': 'https://s2.loli.net/2025/04/26/oqR6TAngZXyVucO.png'
      };
      
      // 返回图床URL
      return cardUrls[formattedId];
    },
    
    // 返回主界面
    returnToMain() {
      this.$emit('return-to-main');
    }
  }
}
</script>

<style scoped>
