<template>
  <div class="main-view">
    <h1>抽卡游戏</h1>
    <div class="rules-card">
      <img :src="rulesCardSrc" alt="规则卡" class="rule-card-image">
    </div>
    
    <div class="code-input-container">
      <input 
        type="text" 
        v-model="identityCode" 
        placeholder="输入识别码（可选）"
        class="identity-code-input"
        maxlength="5"
      >
      <p class="code-tip">输入对方的识别码可避免抽到相同卡牌</p>
    </div>
    
    <button class="draw-button" @click="startGame">开始抽卡</button>
  </div>
</template>

<script>
export default {
  name: 'MainView',
  props: {
    rulesCardSrc: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      identityCode: '',
      errorMessage: ''
    }
  },
  methods: {
    startGame() {
      // 解析识别码，获取排除的卡牌ID
      let excludedCards = [];
      
      if (this.identityCode) {
        try {
          excludedCards = this.parseIdentityCode(this.identityCode);
        } catch (error) {
          alert('识别码无效，将从全部卡牌中抽取');
          excludedCards = [];
        }
      }
      
      this.$emit('start-game', excludedCards);
    },
    
    // 解析识别码
    parseIdentityCode(code) {
      // 检查长度是否为5
      if (code.length !== 5) {
        throw new Error('识别码长度错误');
      }
      
      const charSet = '0123456789abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ';
      const cardIds = [];
      
      // 解析每个字符
      for (let i = 0; i < code.length; i++) {
        const char = code[i];
        const index = charSet.indexOf(char);
        
        // 检查字符是否有效
        if (index === -1) {
          throw new Error('识别码包含无效字符');
        }
        
        // 索引0-53对应卡牌ID 1-54
        const cardId = index + 1;
        cardIds.push(cardId);
      }
      
      return cardIds;
    }
  }
}
</script>

